// require('es6-promise').polyfill();
require('nodelist-foreach-polyfill');
require('formdata-polyfill');

window.addEventListener("DOMContentLoaded", function () {
    "use ctrict";
    // tabs
    let tabs = require("./parts/tabs");
    tabs(".info-header", ".info-header-tab", ".info-tabcontent");

    // Timer
    let deadLine = "2023-12-09";

    function getTimeRemaining(endtime) {
        let t = Date.parse(endtime) - Date.parse(new Date);
        let seconds = Math.floor((t / 1000) % 60);
        let minutes = Math.floor((t / 1000 / 60) % 60);
        let hours = Math.floor(t / (1000 * 60 * 60));

        // let t = (new Date(endtime) / 1000) - (Date.now() / 1000);
        // let seconds = Math.floor(t % 60);
        // let minutes = Math.floor((t / 60) % 60);
        // let hours = Math.floor((t / (60 * 60)));

        // let hoursExample = Math.floor((t/1000/60/60) % 24);
        // let days = Math.floor((t/(1000*60*60*24)));
        return {
            "total": t,
            "hours": hours,
            "minutes": minutes,
            "seconds": seconds,
        }
    };

    function setClock(id, endtime) {
        let timer = document.getElementById(id);
        let hours = timer.querySelector(".hours");
        let minutes = timer.querySelector(".minutes");
        let seconds = timer.querySelector(".seconds");
        let timeInterval = setInterval(updateClock, 1000);

        function updateClock() {
            let t = getTimeRemaining(endtime);
            if (t.total <= 0) {
                clearInterval(timeInterval);
                hours.textContent = "00";
                minutes.textContent = "00";
                seconds.textContent = "00";
            } else {
                // console.log(t.hours.toString().length);
                if (t.hours < 10) hours.textContent = "0" + t.hours;
                else hours.textContent = t.hours;
                if (t.minutes < 10) minutes.textContent = "0" + t.minutes;
                else minutes.textContent = t.minutes;
                if (t.seconds < 10) seconds.textContent = "0" + t.seconds;
                else seconds.textContent = t.seconds;
            }
        }
    }
    setClock("timer", deadLine);

    // Modal
    let more = document.querySelector(".more");
    let descriptionBtnTabs = document.querySelector(".description-btn");
    modal(more);
    modal(descriptionBtnTabs);

    function modal(btn) {
        let overlay = document.querySelector(".overlay");
        let close = document.querySelector(".popup-close");

        btn.addEventListener("click", function () {
            overlay.style.display = "block";
            this.classList.add("more-splash");
            document.body.style.overflow = "hidden";
        })
        close.addEventListener("click", function () {
            overlay.style.display = "none";
            document.body.style.overflow = "";
        })
    }

    // Form
    let message = {
        loading: "Загрузка",
        success: "Спасибо! Скоро мы с вами свяжемся!",
        failure: "Что-то пошло не так..."
    }


    let forms = document.querySelector(".main-form"),
        formContact = document.forms.form,
        input = document.getElementsByTagName("input"),
        divMessage = document.createElement("div");
    divMessage.classList.add("status");
    sendForm(formContact);
    sendForm(forms);

    function sendForm(form) {
        form.addEventListener("submit", function (e) {
            e.preventDefault();
            this.appendChild(divMessage);

            let request = new XMLHttpRequest();
            request.open("POST", "server.php");
            // request.setRequestHeader("Content-type", "application/x-www-form-unlencoded");
            request.setRequestHeader("Content-type", "application/json; charset=utf-8"); // json file
            let formData = new FormData(form);
            let obj = {};
            formData.forEach((item, ind) => {
                obj[ind] = item;
            })
            let json = JSON.stringify(obj);
            // request.send(formData);
            request.send(json);

            request.addEventListener("readystatechange", function () {
                if (this.readyState < 4) divMessage.textContent = message.loading;
                else if (this.readyState == 4 && this.status == 200) divMessage.textContent = message.success;
                else divMessage.textContent = message.failure;
            })
            for (let i = 0; i < input.length; input[i].value = '', i++);
        })
    }

    //slider
    let slides = document.querySelectorAll(".slider-item"),
    prev = document.querySelector(".prev"), next = document.querySelector(".next"),
    dotsWrap = document.querySelector(".slider-dots"), dots = document.querySelectorAll(".dot"),
    count = 0;

    showSlides(0);
    function showSlides(n) {
        if (n >= slides.length) n = 0;
        if (n < 0) n = slides.length - 1;
        count = n;
        slides.forEach((item, ind) => {
            if (n == ind) item.style.display = "block";
            else item.style.display = "none";
        })
        dots.forEach((item, ind) => {
            if (n == ind) item.classList.add("dot-active");
            else item.classList.remove("dot-active");
        })

    }
    prev.addEventListener("click", function() {
        showSlides(count-=1);
    })
    next.addEventListener("click", function() {
        showSlides(count+=1);
    })

    dotsWrap.addEventListener("click", function(e) {
        dots.forEach((item, ind) => {
            if (e.target.classList.contains("dot") && e.target == item) showSlides(ind);
            else return;
        })
    })

    // Calc
    let persons = document.querySelectorAll(".counter-block-input")[0],
        restDays = document.querySelectorAll(".counter-block-input")[1],
        place = document.getElementById("select"),
        totalValue = document.getElementById("total"),
        personSum = 0,
        daysSum = 0,
        total = 0;

        totalValue.textContent = 0;

        persons.addEventListener("input", function() {
            personSum = +this.value;
            total = personSum * 4000 * daysSum;
            // total = (daysSum + personSum) * 4000;
            // if (restDays.value == "") totalValue.textContent = 0;
            // else totalValue.textContent = total;
            totalValue.textContent = total;
        })
        restDays.addEventListener("input", function() {
            daysSum = +this.value;
            total = personSum * 4000 * daysSum;
            // total = (daysSum + personSum) * 4000;
            // if (persons.value == "") totalValue.textContent = 0;
            // else totalValue.textContent = total;
            totalValue.textContent = total;
        })
        place.addEventListener("change", function() {
                let a = total;
                let option = this.options[this.selectedIndex].value;
                // console.log(option, this.value);
                totalValue.textContent = a * option;
        })

});

